class chat {
    static state = class {
        static host = 'http://localhost:212'
        static io = io(this.host + '/socket/chat', {
            query: {
                username: 'tes'
            }
        });
    }
    static store = observable({loginFormUsername: '', loginFormPassword: '', accessToken: null})
    static helper = class {}
    static socket = class {
        static subscribe = (username) => {
            chat
                .state
                .io
                .on('/chat/subscribe/user/' + username, function (msg) {
                    console.log('msg', msg);
                    alert(JSON.stringify(msg));
                });
            chat
                .state
                .io
                .on('welcome', function (msg) {
                    console.log('msg', msg);
                    alert(JSON.stringify(msg));
                });
        }
        static privateMessage = ({to, message}) => {
            const data = {
                to: to,
                message: message
            }
            chat
                .state
                .io
                .emit('/chat/request/sendPrivate', data);
        }
    }
    static http = class {
        static login = (data) => {
            return axios.post(global.app.chat.env.apiHost + '/api/global/login', data)
        }
    }
    static component = class {
        static page = class {
            static home = (props) => {
                if (chat.store.accessToken == null) {
                    props
                        .history
                        .push('/login');
                    return (
                        <div></div>
                    )
                } else {
                    props
                        .history
                        .push(({
                            pathname: '/chatRoom',
                            state: {
                                type: 'private',
                                to: 'ririn'
                            }
                        }));
                    return (
                        <div>jdklajksdl</div>
                    )
                }
            }
            static login = observer((props) => {
                return (
                    <div>
                        <input
                            value={chat.store.loginFormUsername}
                            onChange={e => {
                            chat.store.loginFormUsername = e.target.value
                        }}/><br/>
                        <input
                            value={chat.store.loginFormPassword}
                            onChange={e => {
                            chat.store.loginFormPassword = e.target.value
                        }}/><br/>
                        <button
                            onClick={() => {
                            chat
                                .http
                                .login({username: chat.store.loginFormUsername, password: chat.store.loginFormPassword})
                                .then(response => {
                                    var body = response.data;
                                    if (body.status === global.app.chat.env.success) {
                                        chat.store.accessToken = body.data.accessToken;
                                        chat
                                            .socket
                                            .subscribe(chat.store.loginFormUsername);
                                        props
                                            .history
                                            .push('/');
                                    }
                                })
                        }}>Login</button>
                    </div>
                );
            })
            static chatRoom = observer((props) => {
                const [message,
                    setMessage] = useState('');
                if (props.history.location.state == null) {
                    return (
                        <div>empty</div>
                    )
                } else {
                    const data = props.history.location.state;
                    return (
                        <div>
                            type:{data.type}<br/>
                            to:{data.to}<br/>
                            chat:<br/>
                            <input
                                value={message}
                                onChange={(e) => {
                                setMessage(e.target.value)
                            }}/>
                            <button
                                onClick={() => {
                                chat
                                    .socket
                                    .privateMessage({to: 'ririnmarini', message: message})
                            }}>Send</button>
                        </div>
                    )
                }
            })
        }
    }
    static route = () => {
        const Home = chat.component.page.home;
        const Login = chat.component.page.login;
        const ChatRoom = chat.component.page.chatRoom;
        return (
            <div>
                <Router>
                    <Route exact path="/" render={(props) => <Home {...props}/>}/>
                    <Route path="/login" render={(props) => <Login {...props}/>}/>
                    <Route path="/chatRoom" render={(props) => <ChatRoom {...props}/>}/>
                </Router>
            </div>
        )
    }
    static run = () => {
        return this.route()
    }
}

