class budget {
    static state = class {
        static host = state.host + '/api/budget/'
        static prefix = '/budget'
    }
    static store = observable({
        accessToken: localStorage.getItem('accessToken'),
        group: null,
        login: {
            username: '',
            password: ''
        },
        createTransaction: {
            budgetAccountId: '',
            name: '',
            balance: 0.0
        }
    })
    static http = class {
        static login = (data) => {
            return axios.post(budget.state.host + 'login', data)
        }
        static myGroup = () => {
            return axios.get(budget.state.host + 'user/myGroup', {
                headers: {
                    Authorization: budget.store.accessToken
                }
            })
        }
        static createTransaction = (data) => {
            return axios.post(budget.state.host + 'user/createTransaction', data, {
                headers: {
                    Authorization: budget.store.accessToken
                }
            })
        }
    }
    static helper = class {
        static getMyGroup = () => {
            budget
                .http
                .myGroup()
                .then((result) => {
                    budget.store.group = result.data
                })
        }
    }
    static component = class {
        static page = class {
            static home = (props) => {
                if (budget.store.accessToken == null) {
                    props
                        .history
                        .push(budget.state.prefix + '/login');
                    return (
                        <div></div>
                    )
                } else {
                    budget
                        .helper
                        .getMyGroup()
                    return (
                        <Observer>
                            {() => {
                                if (budget.store.group != null) {
                                    const group = budget.store.group.result.budget_groups[0];
                                    return (
                                        <div>
                                            Nama: {group.name}<br/>
                                            Sisa: {group.balance}<br/>
                                            Update Terakhir: {group.updatedAt}<br/>
                                            <button
                                                onClick={() => {
                                                props
                                                    .history
                                                    .push(budget.state.prefix + '/createTransaction');
                                            }}>Buat Transaksi</button><br/>
                                        </div>
                                    )
                                } else {
                                    return (<div/>)
                                }
                            }}
                        </Observer>
                    )
                }
            }
            static login = observer((props) => {
                return (
                    <div>
                        Username:
                        <input
                            value={budget.store.login.username}
                            onChange={e => {
                            budget.store.login.username = e.target.value
                        }}/><br/>
                        Password:
                        <input
                            value={budget.store.login.password}
                            onChange={e => {
                            budget.store.login.password = e.target.value
                        }}/><br/>
                        <button
                            onClick={() => {
                            budget
                                .http
                                .login({username: budget.store.login.username, password: budget.store.login.password})
                                .then(response => {
                                    var body = response.data;
                                    if (body.status === 'success') {
                                        budget.store.accessToken = body.result.accessToken;
                                        localStorage.setItem('accessToken', body.result.accessToken);
                                        props
                                            .history
                                            .push(budget.state.prefix + '/');
                                    }
                                })
                        }}>Login</button>
                    </div>
                );
            })
            static createTransaction = observer((props) => {
                const account = budget.store.group.result.budget_groups[0].budget_accounts;
                let viewAcc = [];
                viewAcc.push(
                    <option>-Pilih-</option>
                )
                account.forEach((it) => {
                    viewAcc.push(
                        <option value={it.id}>{it.name}</option>
                    )
                });
                return (
                    <div>
                        Account:
                        <select
                            onChange={(e) => {
                            budget.store.createTransaction.budgetAccountId = e.target.value
                        }}>
                            {viewAcc}
                        </select><br/>
                        Nama:
                        <input
                            value={budget.store.createTransaction.name}
                            onChange={e => {
                            budget.store.createTransaction.name = e.target.value
                        }}/><br/>
                        Balance:
                        <input
                            value={budget.store.createTransaction.balance}
                            onChange={e => {
                            let float = parseFloat(e.target.value);
                            if (!isNaN(float)) {
                                budget.store.createTransaction.balance = parseFloat(e.target.value)
                            }
                        }}/><br/>
                        <button
                            onClick={() => {
                            budget
                                .http
                                .createTransaction(budget.store.createTransaction)
                                .then((it) => {
                                    let response = it.data;
                                    if (response.status === 'success') {
                                        alert('success');
                                        props
                                            .history
                                            .push(budget.state.prefix + '/');
                                    }
                                })
                        }}>Buat Transaksi</button><br/>
                    </div>
                )
            })
        }
    }
    static route = () => {
        const Home = budget.component.page.home;
        const Login = budget.component.page.login;
        const CreateTransaction = budget.component.page.createTransaction;
        return (
            <div>
                <Router>
                    <Route
                        exact
                        path={budget.state.prefix + "/"}
                        render={(props) => <Home {...props}/>}/>
                    <Route
                        path={budget.state.prefix + "/login"}
                        render={(props) => <Login {...props}/>}/>
                    <Route
                        path={budget.state.prefix + "/createTransaction"}
                        render={(props) => <CreateTransaction {...props}/>}/>
                </Router>
            </div>
        )
    }
    static run = () => {
        return this.route()
    }
}

export default class main {
    static home = (props) => {
        return (
            <button
                onClick={() => {
                props
                    .history
                    .push(budget.state.prefix + '/');
            }}>Budget</button>
        )
    }
    static route = () => {
        const Home = main.home;
        const Budget = budget.run;
        return (
            <Router>
                <Route exact path={"/"} render={(props) => <Home {...props}/>}/>
                <Budget/>
            </Router>
        )
    }
    static run = () => {
        const rootElement = document.getElementById("root");
        const MainRoute = this.route;
        ReactDOM.render(
            <React.StrictMode>
            <MainRoute/>
        </React.StrictMode>, rootElement);
    }
}

