const reducer = (state, action) => {
    console.log(action);
    switch (action.type) {
        case "main":
            return {
                ...state,
                globalData: action.data
            };
        case "increment":
            return {
                ...state,
                count: state.count + 1
            };
        case "decrement":
            return {
                ...state,
                count: state.count - 1
            };
        default:
            return state;
    }
};
const initialState = {
    count: 0,
    globalData: null
};
const {dispatch, useGlobalState} = createStore(reducer, initialState);

