const personalMediaStyle = makeStyles(theme => ({
    container: {
        // maxWidth: 345,
        width: "100%",
        bacgroundColor: "blue"
    },
    media: {
        height: 200
    },
    item: {
        marginBottom: theme.spacing(2)
    }
}));

function PersonalMediaCard() {
    const classes = personalMediaStyle();
    const firstUpdate = useRef(true);
    const [globalData] = useGlobalState("globalData");
    useLayoutEffect(() => {
        if (firstUpdate.current) {
            PersonalApi.getPersonalInfo();
            firstUpdate.current = false;
            return;
        }
        console.log("componentDidUpdateFunction");
    });
    let personal = {};
    if (globalData !== null) {
        personal = globalData
            .keyValue
            .find(a => (a.key = "personalInfo"));
        const value = JSON.parse(personal.value);
        personal = {
            ...personal,
            value
        };
    }
    // let a=(<div/>) for(let i=0; i<100;i++){   a= }
    if (globalData === null) {
        return <div/>;
    } else {
        return (
            <Grid
                container
                component="home"
                className={classes.container}
                direction="column">
                <Grid
                    item
                    xs={12}
                    sm={8}
                    md={5}
                    component={Paper}
                    elevation={6}
                    className={classes.item}
                    square>
                    <Card>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image="https://source.unsplash.com/random"
                                title="Contemplative Reptile"/>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {personal.value.nickname}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    Title: {personal.value.title}
                                    <br/>
                                    City
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Button size="small" color="primary">
                                Share
                            </Button>
                            <Button size="small" color="primary">
                                Learn More
                            </Button>
                        </CardActions>
                    </Card>
                </Grid>
                <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                    <Card className={classes.root}>
                        <CardActionArea>
                            <CardMedia
                                className={classes.media}
                                image="https://source.unsplash.com/random"
                                title="Contemplative Reptile"/>
                            <CardContent>
                                <Typography gutterBottom variant="h5" component="h2">
                                    {personal.value.nickname}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    Title: {personal.value.title}
                                    <br/>
                                    City
                                </Typography>
                            </CardContent>
                        </CardActionArea>
                        <CardActions>
                            <Button size="small" color="primary">
                                Share
                            </Button>
                            <Button size="small" color="primary">
                                Learn More
                            </Button>
                        </CardActions>
                    </Card>
                </Grid>
            </Grid>
        );
    }
}
